	module.exports = {"data":{"markdownRemark":{"html":"<p>The Black Hill of Kutch or ‘Kala Dungar’ as it is known in the local lingo, is the highest point of Kutch district with a rich history in itself! Legend has it that Lord Dattatreya once came to this place &#x26; fed the Jackals here with his own body which kept on regenerating continuously, hence the Dattatreaya Temple on the top of the hill. Even today, the temple priest offers the Prasad to Jackals at a specific spot near the temple after the evening ceremony. Apart from the divine significance, it is also popular as probably the only place in Kutch which offers an amazing panoramic view of the White Rann of Kutch &#x26; one of the closest civilian points to the Indo-Pak border.</p>\n<p>Recently, strange phenomenon, was observed at Kalo Dungar, when some visitors noticed that their vehicles were hurtling down the hill at speeds over 80 km/h which was very strange. And this too, with the ignition switched off.</p>\n<p>Our expertise in arranging just the perfect trip to Kala Dungar in Kutch. Our local guides can help you unravel the mystery of the Jackals, show you the very best views the Black Hills of Kutch has to offer &#x26; much more!</p>","frontmatter":{"title":"Kala Dungar (Black Hill)"}}},"pathContext":{"slug":"/blackHill/"}}

